.harrast {
  &__img {
    width: 100%;
    aspect-ratio: auto;
    margin-bottom: 60px;
    & > img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    &--sm {
      margin-bottom: 0;
    }
  }
  // .harrast__title

  &__title {
    margin-top: 60px;
    margin-bottom: 32px;
  }

  // .harrast__text

  &__text {
    font-family: $secondFont;
    font-style: normal;
    font-weight: 900;
    line-height: 30px;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    &--reverse {
      flex-direction: row-reverse;
    }
  }
  &__item {
    display: flex;
    flex: 0 0 47%;
    flex-direction: column;
  }
  // .harrast__link-wrap

  &__link-wrap {
  }

  // .harrast__link

  &__link {
  }

  // .harrast__arrow

  &__arrow {
  }

  // .harrast__link-text

  &__link-text {
    font-family: $mainFont;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: $mainFontColor;
  }
}
